.container {
    display: grid;
    padding: 2rem;
    grid-template-columns: 300px 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
    font: 500 100%/1.5 system-ui;
  }
img {
    max-width: 100%;
    height: auto;
  }
h3 {
    text-align: center;
    font-size: 30px;
    font-style: italic;
    font-family: 'Times New Roman', Times, serif;
}
footer {
    text-align: center;
    padding: 3px;
    background-color: DarkSalmon;
    color: white;
  }
ul {
  list-style-type: square;
}
.one {
    font-family: fantasy;
    color: rgb(52, 3, 189);
}

  

h2 {
    font-size: 14px;
}
input  {
    display: inline;
    position:relative
}

#container div.gallery {
    margin: 10px;
    border: 1px solid #ccc;
    
    width: 180px;
  }
  
  #container div.gallery:hover {
    border: 1px solid #777;
  }
  
  #container div.gallery img {
    width: 90%;
    height: auto;
  }
  
  #container div.desc {
    padding: 15px;

    text-align: right;
  }
body {
    background: linear-gradient(#FFB8DE, #B8C1FF);
    padding: 20px;
    margin: 0;
    

  }
  h1 {
    font-family: cursive;
    font-size: 30px;
  }
  h2, p, ul, li {

    font-family: Palatino;
    font-size: 20px;
  }

  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.header {
    background-color: #111;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 10;
    margin: 10;
  }
  .content li {
    margin-bottom: 30px;
  }
